<template>
	<div ref="wrapper">
		<div>
			<slot></slot>
		</div>
	</div>
</template>

<script>
	import BScroll from "better-scroll"

    export default {
        name: "Scroll",
		props:{
			probeType:{
				type:Number,
				default:0
			},
			data: {
				type: Array,
				default: null
			},
		},
		data(){
			return{
				scroll:null
			}
		},
		mounted(){
			//
			let wrapper=this.$refs.wrapper
			//bscroll
			this.scroll=new BScroll(wrapper,{
				probeType:this.probeType,			//
				click:true,							//
				pullUpLoad:true,					//
				observeDOM:true
			})
			
			//
			this.scroll.on("scroll",(position)=>{
				//
				this.$emit("bscroll",position)
			})
			//
			this.scroll.on("pullingUp",()=>{
				//
				this.$emit("pullingup")
			})
		},
		methods:{
			//
			scrollTo(x, y, time=300) {
               this.scroll && this.scroll.scrollTo(0, 0, time)
            },
			//
			finishPullUp(){
				this.scroll && this.scroll.finishPullUp()
			},
			//
			refresh(){
				//console.log("")
				this.scroll && this.scroll.refresh()
			}
		},
		watch:{
			data(){
				setTimeout(() => {
					this.refresh()
				},500)
			}
		}
    }
</script>

